(function () {
	'use strict';

	angular
		.module('asylumjourneyFrontend')
		.filter('stage', stage);

	function stage() {

		return function stageFilter(items, stage) {
			var filtered = [];

			angular.forEach(items, function(item) {
				angular.forEach(item._embedded.stages, function(itemStage) {
					if (itemStage.id === stage) {
						filtered.push(item);
					}
				});
			});
			return filtered;
		}
	}
})();

(function () {
  'use strict';

  angular
    .module('asylumjourneyFrontend')
    .filter('serviceUser', serviceUser);

  function isInArray (array, id) {
    return array.some(function (e) { return e.id === id;});
  }

  function serviceUser() {

    return function serviceUserFilter(items, serviceUser) {
      if (!serviceUser.length) {return items;}

      var filtered = [];

      angular.forEach(items, function(item) {

        angular.forEach(item._embedded.serviceUsers, function(itemServiceUser) {

          angular.forEach(serviceUser, function (id) {

            if (itemServiceUser.id === id) {

              if ( isInArray(filtered, item.id) ) {
                return;
              }
              filtered.push(item);
              return;
            }

          });     

        });
      });

      return filtered;
    };
  }
})();

(function () {
  'use strict';

  angular
    .module('asylumjourneyFrontend')
    .filter('provider', provider);

  function isInArray (array, id) {
    return array.some(function (e) { return e.id === id;});
  }

  function provider() {

    return function providerFilter(items, provider) {
      if (!provider.length) {return items;}

      var filtered = [];

      angular.forEach(items, function(item) {

        angular.forEach(item._embedded.providers, function(itemProvider) {

          angular.forEach(provider, function (id) {

            if (itemProvider.id === id) {

              if ( isInArray(filtered, item.id) ) {
                return;
              }
              filtered.push(item);
              return;
            }

          });     

        });
      });

      return filtered;
    };
  }
})();

(function () {
	'use strict';

	angular
		.module('asylumjourneyFrontend')
		.filter('category', category);

	function category() {

		return function categoryFilter(items, category) {
			var filtered = [];

			angular.forEach(items, function(item) {
				angular.forEach(item._embedded.categories, function(itemCategory) {
					if (itemCategory.id === category) {
						filtered.push(item);
					}
				});
			});

			return filtered;
		}
	}
})();

(function() {
	'use strict';

	angular
		.module('asylumjourneyFrontend')
		.run(runBlock);

	/** @ngInject */
	function runBlock(ngDialog, $rootScope) {

		var deregistrationCallback = $rootScope.$on('$routeChangeStart', function(next, current) {
			if (next !== current){
				ngDialog.closeAll();
				$rootScope.dialogOpen = false;
			}
		});


		$rootScope.$on('$destroy', deregistrationCallback)
	}

})();

(function() {
  'use strict';

  angular
    .module('asylumjourneyFrontend')
    .config(routeConfig);

  function routeConfig($routeProvider) {
    $routeProvider
      .when('/', {
        templateUrl: 'app/home/home.html',
        controller: 'HomeController',
        controllerAs: 'home'
      })
      .when('/tool', {
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .when('/service/:serviceId', {
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .otherwise({
        redirectTo: '/'
      });
  }

})();

